import React from "react";
import ReactDOM from "react-dom";
import "./styles/styles.css";
import "./icons/styles.css";
import Navbar from "./components/Navbar";
import Content from "./components/Content";

class App extends React.Component {

  constructor (props) {
    super(props)

    this.state = {
      bkgOverlay: "background-overlay-hide",
      addwindisplay: "add-file-container"
    }
    this.handleClick = this.handleClick.bind(this)
    this.clickOutside = this.clickOutside.bind(this)
    this.hideWindow = this.hideWindow.bind(this)
  }

  hideWindow () {
    document.removeEventListener('mousedown', this.clickOutside)
    this.setState({
      bkgOverlay: "background-overlay-hide",
      addwindisplay: "add-file-container add-file-container-animout"
    })
  }

  clickOutside (e) {
    const formId = document.getElementById('form-id');
    if (!formId.contains(e.target)) {
        this.hideWindow()
    }
  }

  handleClick () {
    const bkgOverlayState = this.state.bkgOverlay === "background-overlay-hide" ? "background-overlay" : "background-overlay-hide"
    const addWinState = this.state.addwindisplay === "add-file-container" || this.state.addwindisplay === "add-file-container add-file-container-animout" ? "add-file-container add-file-container-animin" : "add-file-container"
    document.addEventListener('mousedown', this.clickOutside)
    this.setState({
      bkgOverlay: bkgOverlayState,
      addwindisplay: addWinState
    })
  }

  render () {
    return (
      <div>
        <Navbar action={this.handleClick} />
        <Content display={this.state.bkgOverlay} windisplay={this.state.addwindisplay} submit={this.hideWindow}/>
      </div>
    )
  }
}

ReactDOM.render(
  <App />, document.getElementById("root")
)
