import React from "react";

class NavButton extends React.Component {
  render () {
    return (
      <button className={this.props.type} data-icon={this.props.icon} onMouseDown={this.props.action} onBlur={this.props.exitAction} disabled={this.props.status}>
        <span>
          {this.props.text}
        </span>
      </button>
    )
  }
}

export default NavButton;
