import React from "react";
import NavButton from "./NavButton";

class ColorPost extends React.Component {

  constructor (props) {
    super(props)

    this.state = {
      title: this.props.post.title,
      mainColor: this.props.post.mainColor,
      secondColor: this.props.post.secondColor,
      thirdColor: this.props.post.thirdColor,
      fourthColor: this.props.post.fourthColor,
      menu: "none",
      moreButton: "more-btn"
    }
    this.toggleButtonState = this.toggleButtonState.bind(this)
    this.delete = this.delete.bind(this)
    this.exitState = this.exitState.bind(this)
  }

  toggleButtonState () {
    const updatedMenuState = this.state.menu === "none" ? "block" : "none"
    const moreButtonState = this.state.menu === "none" ? "more-btn-on" : "more-btn"
    this.setState({
      menu: updatedMenuState,
      moreButton: moreButtonState
    })
  }

  delete(key){
    this.props.delete(key)
  }

  exitState () {
    this.setState({
      menu: "none",
      moreButton: "more-btn"
    })
  }

  render () {
    return (
      <article className="img-post">
        <NavButton type="check-icon" icon="c"/>
        <div className="img-container">
          <div style={{"position":"absolute","top":"0","height":"100%","width":"100%", "border":"1px solid #dcdfe0"}}>
            <div className="color-bar" style={{"backgroundColor":this.state.mainColor}}></div>
            <div className="color-bar" style={{"backgroundColor":this.state.secondColor}}></div>
            <div className="color-bar" style={{"backgroundColor":this.state.thirdColor}}></div>
            <div className="color-bar" style={{"backgroundColor":this.state.fourthColor}}></div>
          </div>
          <span className="more-container">
            <NavButton type={this.state.moreButton} icon="k" action={this.toggleButtonState} exitAction={this.exitState}/>
            <ul id="options-dropdown" style={{"display":this.state.menu}}>
              <li><NavButton type="options-icons" icon="h" text="Copy SVG"/></li>
              <li><NavButton type="options-icons" icon="f" text="Download"/></li>
              <li><NavButton type="options-icons" icon="b" text="Delete" action={()=> this.delete(this.props.post.key)}/></li>
            </ul>
          </span>
        </div>
        <p className="post-name">{this.state.title}</p>
      </article>
    )
  }
}

export default ColorPost;
