import React from 'react';
import NavButton from './NavButton';

class AddFile extends React.Component {

  constructor (props) {
    super(props)

    this.state = {
      fileuploaded: {},
      filecheck: false,
      note: "Note: Upload image to generate a color palette"
    }

    this.add = this.add.bind(this)
    this.uploaded = this.uploaded.bind(this)
  }

add(event){

  event.preventDefault()


  let filename = ""

  if (this.state.filecheck) {
    if (event.target.title.value !== "") {
      filename = event.target.title.value;
    } else {
      if (this.state.fileuploaded.name.indexOf(".") !== -1) {
        const ext = this.state.fileuploaded.name.indexOf(".");
        filename = this.state.fileuploaded.name.slice(0, ext)
      } else {
        filename = this.state.fileuploaded.name;
      }
    }
    this.props.add(this.state.fileuploaded, filename);
    event.target.file.value = "";
    event.target.title.value = "";
    this.setState({
      fileuploaded: {},
      filecheck: false,
      note: "Note: Upload image to generate a color palette"
    })
  } else {
    this.setState({
      note: "Error: Oops! Looks like you forgot to upload a file"
    });
  }

}

uploaded (filelist){
  const file = filelist[0];
  this.setState({
    fileuploaded: file,
    filecheck: true,
    note: "Status: Ready to create beautiful colors"
  })
}

  render(){
    return (
      <div className={this.props.display} id="upload-file-cont">
        <div className={this.props.windisplay} id="upload-file-win">
          <form id="form-id" onSubmit={this.add}>
            <div className="add-file-window">
              <div className="add-file-button-group">
                <NavButton type="add-selector-button add-selector-button-off" icon="l" text="Text" status={true} />
                <NavButton type="add-selector-button add-selector-button-off" icon="i" text="Link" status={true} />
                <NavButton type="add-selector-button add-selector-button-off" icon="d" text="File" status={true} />
                <NavButton type="add-selector-button add-selector-button-off" icon="g" text="Stack" status={true} />
                <NavButton type="add-selector-button add-selector-button-on add-selector-button-full" icon="h" text="Image to Color Palette" status={true} />
              </div>
              <fieldset>
                <input name="title" className="text-field" type="text" placeholder="Title (optional)" disabled={this.props.loading ? true : false}/>
              </fieldset>
              <fieldset>
                <input name="file" id="file" style={{"width":"100%"}} type="file" accept=".jpg,.png" onChange={(event)=> { this.uploaded(event.target.files)}} disabled={this.props.loading ? true : false}/>
                <span className="upload-note">{this.state.note}</span>
              </fieldset>
            </div>
            <NavButton type={this.props.loading ? "add-file-button-loading" : "add-file-button"} icon="a" text={this.props.loading ? "" : "Dropmark This"} status={this.props.loading ? true : false}/>
          </form>
          <NavButton type="close-button" icon="r" />
        </div>
      </div>
    )
  }

}

export default AddFile
