import React from "react";
import FilterBar from "./FilterBar";
import ColorPost from "./ColorPost";
import postData from "../postData";
import AddFile from "./AddFile";
import ColorThief from "../scripts/color-thief";


class Content extends React.Component {

  constructor (props) {
    super(props)

    this.state = {
      posts: postData
    }
    this.deletePost = this.deletePost.bind(this)
    this.addPost = this.addPost.bind(this)
  }

  deletePost(key){
    var filteredPosts = this.state.posts.filter( function (item) {
      return (item.key !== key)
    })
    this.setState({
      posts: filteredPosts,
      loading: false
    })
  }

  addPost(file, name){

    this.setState({
      loading: true
    })

    const img = document.createElement("img");
    img.classList = "color-grab";
    img.id = "img-id"
    img.file = file;

    const colorThief = new ColorThief();

    const reader = new FileReader();
    reader.onload = function(e){
      img.src = e.target.result

      img.onload = function (){
        addPalette(colorThief.getPalette(img, 10, 1))
      }
    }
    reader.readAsDataURL(file);

    const addPalette = (color) => {
      const newPost = {
        key: Date.now(),
        title: name,
        mainColor: "rgb(" + color[0].toString() + ")",
        secondColor: "rgb(" + color[1].toString() + ")",
        thirdColor: "rgb(" + color[2].toString() + ")",
        fourthColor: "rgb(" + color[3].toString() + ")"
      }
      this.setState((prevState) => {
        return {posts: prevState.posts.concat(newPost)}
      })
      this.props.submit()
      setTimeout(()=>this.setState({
        loading: false
      }),300)
    }

  }

  render () {
    const postItems = this.state.posts.map(post => <ColorPost key={post.key} post={post} delete={this.deletePost}/>)
    return (
      <div>
      <section style={{"paddingTop":"65px"}}>
        <FilterBar />
        <div style={{"padding":"0 20px 40px 20px"}}>
          {postItems}
        </ div>
      </section>
      <AddFile loading={this.state.loading} display={this.props.display} add={this.addPost} windisplay={this.props.windisplay}/>
      </div>
    );
  }
}

export default Content;
